.endPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  //background: #45B3E9;
  padding: 0 20px;
  //height: 100vh;
  justify-content: space-between;

  .cloud{
    position: absolute;
    &.one{
      top: 27.42px;
      left: -10px;
      width: 63.65px;
      height: 31.82px;
    }
    &.two{
      top: 20.79px;
      right: 48px;
      width: 47.74px;
      height: 23.87px;
    }
    &.three{
      top: 108px;
      right: 0;
    }
  }
  .Skip{
    color: white;
    text-decoration: underline;
    margin-top: 16px;
    font-family: "TT Commons";
    font-style: normal;
    font-weight: 400;
  }
  p {
    max-width: 280px;
    font-family: "TT Commons";
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 26px;
    text-align: center;

    color: #FFFFFF;
  }

  .textMainApp {
    z-index: 200;
    text-align: center;
    margin-top: 25px;
    margin-bottom: 25px;
    font-weight: 400;
    font-size: 62px;
    line-height: 74px;
    font-family: "QIWIPixel-Regular";
    color: white;
    -webkit-text-stroke: 1px black;
    text-shadow: 2px 2px 0 #000,
    -1px -1px 0 #000,
    1px -1px 0 #000,
    -1px 1px 0 #000,
    1px 1px 0 #000;
  }

  button {
    //margin-bottom: 80px;
    font-family: 'TT Commons';
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 37px;
    display: flex;
    align-items: center;
    text-align: center;
    padding: 15px 93px 15px 93px;
    color: #FFFFFF;
    background: rgba(243, 137, 34, 0.47);
    border: 1.24971px solid #FFFFFF;
    border-radius: 17.496px;
    &.submitForm{
      background: #FF8000;

    }
  }
  .count {
    //margin-bottom: 29px;
    display: flex;
    z-index: 100;
    width: 100%;
    justify-content: space-around;
    .contNumber {
      display: flex;
      flex-direction: column;
      span {
        font-family: 'TT Commons';
        font-style: normal;
        font-weight: 800;
        font-size: 64px;
        line-height: 74px;
        text-align: center;

        color: #FFFFFF;
      }
      p{
        font-size: 24px;
      }
    }
  }
  .checkbox{
    display: flex;
    width: 100%;
    margin-top: 20px;
    font-family: 'TT Commons';
    font-style: normal;
    font-weight: 300;
    font-size: 9.86148px;
    line-height: 11px;

    color: #FFFFFF;

    .custom-checkbox{
      position: absolute;
      z-index: -1;
      opacity: 0;
    }
    .custom-checkbox+label {
      display: inline-flex;
      align-items: center;
      user-select: none;
    }
    .custom-checkbox+label::before {
      content: '';
      display: inline-block;
      width: 21px;
      height: 21px;
      flex-shrink: 0;
      flex-grow: 0;
      border: 1px solid #adb5bd;
      border-radius: 0.25em;
      margin-right: 20px;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 50% 50%;
    }
    .custom-checkbox:checked+label::before {
      border-color: #FF8000;
      background-color: #FF8000;
    }
    .custom-checkbox:not(:disabled):not(:checked)+label:hover::before {
      border-color: #b3d7ff;
    }
    /* стили для активного состояния чекбокса (при нажатии на него) */
    .custom-checkbox:not(:disabled):active+label::before {
      background-color: #b3d7ff;
      border-color: #b3d7ff;
    }
    /* стили для чекбокса, находящегося в фокусе */
    .custom-checkbox:focus+label::before {
      box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
    }
    /* стили для чекбокса, находящегося в фокусе и не находящегося в состоянии checked */
    .custom-checkbox:focus:not(:checked)+label::before {
      border-color: #80bdff;
    }
    /* стили для чекбокса, находящегося в состоянии disabled */
    .custom-checkbox:disabled+label::before {
      background-color: #e9ecef;
    }
  }
}
.score{
