  1px -1px 0 #000,
  -1px 1px 0 #000,
  1px 1px 0 #000;
  position: fixed;
  left: 20px;
  top: 0;
  z-index: 1000;
  font-style: normal;
  line-height: 115px;
}
.LinkCopied{
  transition: transform 0.5s;
  width: 0;
  height: 0;
  transform: scale(0);
  background: rgba(128, 68, 199, 0.6);
  backdrop-filter: blur(15px);
  &.open{

    z-index: 500;

    position: absolute;
    width: 100vw;
    height: 100vh;
    transform: scale(1);
    .blockLink{
      align-content: space-around;
      align-items: center;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: space-around;
      img{
        bottom: inherit;
      }
      span{
        margin-top: 170px;
        font-family: "TT Commons";
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        line-height: 28px;
        text-align: center;
        color: #FFFFFF;
      }

    }
  }
}
@media screen and (max-width: 800px){
  .LinkCopied {

    &.open {
      .blockLink {
        display: flex;
        flex-direction: column;
        justify-content: center;
        span{
          margin-top:30px;
          font-size: 32px;
          line-height: 37px;
          display: flex;
          align-items: center;
          text-align: center;
          color: #FFFFFF;
        }
      }
    }
  }
}
